@use '../../variables';

button {
    border: none;
    outline: none;
    background-color: variables.$main-color;
    color: variables.$secondary-color-white;
    border-radius: 5px;
}

button:hover{
    background-color: variables.$main-color-green;
}

.button-primary {
    width: 127px;
    height: 40px;
    font-size: 16px;
    margin-bottom: 5px;
}
