@use '../../variables';

* {
    box-sizing: border-box;
} 

.wrapper-intro {
    display: flex;
    align-items: stretch;
}

.icon-bar {
    position: sticky;
    padding: 3px 5px;
    top: 5%;
    height: fit-content;
    border-radius: 0 5px 5px 0;
    color: variables.$main-color-white;
    background-color: variables.$main-color;
}

.icon-bottom {
    position: fixed;
    padding: 5px 10px;
    bottom: 0;
    right: 1.5rem;
    border-radius: 5px 5px 0 0;
    align-self: flex-end;
    color: variables.$main-color-white;
    background-color: variables.$main-color;
}

.section_cv--subtitle {
    font-size: 16px;
    font-weight: 700;
    padding-bottom: 5px;
}

a.section_cv--subtitle {
    color: inherit;
}
