@use '../../variables';

.nav-link {
    padding: 20px 0;
}

.navbar-nav a {
    text-decoration: none;
    font-size: 16px;
    color: #667081;
}

.navbar-nav {
    a:hover {
        color: variables.$main-color-green;
    }
    .active {
        color: variables.$main-color-green;
    }
} 

.navbar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.navbar { 
    h2 {
        text-align: center;
        font-size: 16px;
        font-weight: normal;
    }
    .avatar-img {
        height: 100px;
        width: 100px;
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 10px;
    }
}
.avatar {
    font-weight: normal;
    color: variables.$secondary-color-white;
    padding: 20px 5px;
}

.navbar-nav {
    margin: 0px;
}

.navbar-wrapper--wrapper {
    position: sticky;
	top: 0;
}

.navbar-dark {
    flex: 0 0 200px;
    background-color: variables.$main-color;
}

.navbar-dark.hidden {
    display: none;
    transition:width .5s easer;
}

.navbar-nav--icon {
    width: 17px;
    height: 18px;
    padding-right: 20px;
}

.button-nav {
    margin: 10px;
    padding: 10px;
    position: sticky;
    bottom: 0px;
}

@media (max-width: variables.$max-width-general) {

    .text:not(.button-primary) {
        display: none;
    }
    
    .navbar {
        .avatar-img {
            height: 40px;
            width: 40px;
            margin-bottom: 0px;
        }
        .avatar {
            h2 {
                display: none;
            }
        }

        ul {
            padding: 0px;
            padding-left: 2px;
        }
    }

    .navbar-dark {
        flex: 0;
    }

    .navbar-bar {
        display: inline-flex;
    }

    .nav-link {
        padding-left: 25%;
    }
}
