@use '../../variables';

.overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: variables.$secondary-color-white;
    overflow: hidden;
    width: 100%;
    height: 0;
    opacity: .9;
    transition: .5s ease;
}
  
.filter-item:hover .overlay {
    height: 100%;
}
  
.info-portfolio {
    color: variables.$main-color;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 40%;
    height: 100%;
    transform: translate(-50%, -50%);
    text-align: left;
    overflow-y: scroll;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.info-portfolio--title {
    font-size: 16px;
    color: variables.$main-color-green;
}

.info-portfolio--title .info-portfolio--description {
    padding-top: 10px;
}

.info-portfolio--link {
    text-decoration: none;
    font-size: 14px;
    color: variables.$main-color-green;
}
