@use '../../variables';

.feedback-reporter {
    padding: 10px 0px 10px 0px;
    display: inline-flex;
    gap: 12px;
}

.feedback-content {
    padding: 10px;
    background-color: variables.$main-color-white;
}

.feedback-reporter {
    h2, h3, h4, a {
        font-size: 12px;
        font-weight: normal;
        align-self: center;
    }
}

.feedback-reporter a {
    text-decoration: none;
    color: variables.$main-color-green;
}

.feedback-reporter img {
    height: 50px;
    width: 50px;
}
