
@use '../../variables';

.wrapper-timeline .title {
    padding-top: 10px;
    font-size: 16px;
}

.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 4px;
  background-color: variables.$main-color-green;
  top: 35px;
  bottom: 0;
  left: 17px;
  margin-left: -3px;
}
   
.container {
  padding: 0px 20px 20px 0px;
  position: relative;
  background-color: inherit;
  width: 100%;
  left: 15px;
}

.container::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 10px;
  width: 0;
  z-index: 1;
  left: -10px;
  border: medium solid variables.$main-color-white;
  border-width: 10px 10px 10px 0;
  border-color: transparent variables.$main-color-white transparent transparent;
}

.content {
  padding: 0px 10px 10px;
  background-color: variables.$main-color-white;
  position: relative;
  border-radius: 6px;
}

.wrapper-timeline {
  display: flex;
  flex-direction: row;
}

.loading-logo {
  height: 4vmin;
  pointer-events: none;
}
.error-notification {
  color: variables.$error-message-color;
  text-align: center;
  font-weight: 600;
}

@media (prefers-reduced-motion: no-preference) {
  .loading-logo {
    animation: App-logo-spin infinite 1s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: variables.$max-width-general) {

  .container {
    width: 100%;
    padding-left: 20px;
    padding-right: 25px;
  }

  .container::before {
    left: 10px;
    border: medium solid variables.$main-color-white;
    border-width: 10px 10px 10px 0;
    border-color: transparent variables.$main-color-white transparent transparent;
  }

  .wrapper-timeline {
    display: flex;
    flex-direction: row;
  }
}
