.iso-nav {
    text-align: left;
} 

.iso-nav  ul, li {
    list-style: none;
    margin: 0;
    padding-right: 10px;
    display: inline-block;
    cursor: pointer;
}

.iso-nav li {
    perspective: 400px;
}

.filter-container {
    width: 100%;
    height: auto;
    margin: 0;
}

.filter-container:after {
    content: '';
    display: block;
    clear: both;
  }

.img-responsive {
    width: 308px;
    height: 190px;
}  

.filter-container .filter-item {
    float: left;
    padding-right: 5px;
}

.iso-nav li::after {
    padding-right: 20px;
    padding-left: 20px;
    content: "/";
    float: right;
}
    
.iso-nav li:last-child::after {
    display: none;
}
