@use '../../variables';

.wrapper-section-home {
    background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(../../assets/images/intro.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    color: variables.$secondary-color-white;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 20px;
    gap: 20px;
}

.wrapper-aboutMe {
    display: contents;
    img {
        height: 163px;
    }
}

.wrapper-aboutMe h2 {
    font-size: 45px;
    font-weight: bold;
}

.wrapper-aboutMe h3 {
    font-size: 25px;
    font-weight: normal;
}

.wrapper-aboutMe p {
    font-size: 14px;
}
