@use '../../variables';

.skills-container {
    width: 100%;
    margin-bottom: 5px;
  }

.skills {
    text-align: left;
    padding: 10px;
    color: variables.$secondary-color-white;
    background-color: variables.$main-color-green;
}

.wrapper-add-skills {
    position: absolute;
    right: 0px;
    top: 15px;
    .button-primary.text{
        padding-left: 5px;
    }
}

.skills-form {
    margin-bottom: 5px;
    padding: 10px 10px;
    border: 2px solid variables.$main-color-green;
    border-radius: 5px;
    display: flex;
    flex-direction: column;

    &-item {
        display: inline;
        padding: 10px 0px;
    }

    &-error-message{
        color: variables.$error-message-color;
        font-size: 12px;
        padding-bottom: 5px;
    }

     label {
        padding-right: 5px;
        font-weight: 600;
    }
}

.skill-wrapper {
    padding: 0px;
    position: relative;
    ul{
        position: relative;
        padding: 0px;
        width: 100%;
        display: inline-flex;
        border-bottom: variables.$skills-border solid variables.$main-color;
        border-left: variables.$skills-border solid variables.$main-color;
        border-right: variables.$skills-border solid variables.$main-color;
    }

    li {
        position: relative;
        list-style: none;
        height: 10px;
        width: 100%;
        padding-top: 10px;
        text-align: center;

        &:first-child {
            padding-left: 0px;
            text-align: left;
            width: 30%;
        }

        &:last-child {
            padding-right: 0px;
            text-align: right;
            width: 30%;
        }

        &:not(:first-child,:last-child):before{
            content: "";
            position: absolute;
            height: 100%;
            width: variables.$skills-border;
            background: variables.$main-color;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

#skills {
    position: relative;
}
@media (max-width: 470px) {

    .skills-form {
        &-item {
            display: contents;
        }
    }
}
  